<template>
  <div class="app-page page-content-bottom-menu">
    <div>
      <van-field v-model="openId" label="openId" placeholder="请输入openid"/>
    </div>
    <div>
      <van-button type="primary"  @click="login">登陆阿</van-button>
    </div>
  </div>
</template>
<script>
import {setToken} from '@/utils/auth'
export default {
  name: 'Login',
  data () {
    return {
      openId: 'omfNLvyu3j4rrE6HxokGWUWYKGSs',
      loginForm: {
        username: '',
        password: ''
      },
      loading: false
    }
  },
  components: {
  },
  methods: {
    login () {
      const paramUrl = '?openId=' + this.openId
      this.$post(this.$api.login + paramUrl, {})
        .then(res => {
          if (res.code === '0') {
            setToken(res.data)
            setTimeout(() => {
              this.$toast.success({
                duration: 1000, // 持续展示 toast
                forbidClick: true, // 禁用背景点击
                loadingType: 'spinner',
                message: '登陆成功'
              })
              this.$router.push({path: '/'})
            }, 1000)
          } else {
            this.$toast.fail('登陆失败')
          }
        })
        .catch(() => {
          this.$toast.fail('登陆错误')
        })
    }
  }
}
</script>
<style rel='stylesheet/scss' lang='scss' scoped>
</style>



